import React from 'react'

function EachCountry(props) {
  return (
    <>
      <div className={props.darkMode ? 'eachCountry eachCountry--darkMode' : 'eachCountry'}>
        <h2 className={props.darkMode ? 'eachCountry__country eachCountry__country--darkMode' : 'eachCountry__country'}>{props.countriesDetails.country}</h2>
        <h3 className={props.darkMode ? 'eachCountry__text eachCountry__text--darkMode' : 'eachCountry__text'}>Cases: {props.countriesDetails.cases}</h3>
        <h3 className={props.darkMode ? 'eachCountry__text eachCountry__text--darkMode' : 'eachCountry__text'}>Deaths: {props.countriesDetails.deaths}</h3>
        <h3 className={props.darkMode ? 'eachCountry__text eachCountry__text--darkMode' : 'eachCountry__text'}>Recovered: {props.countriesDetails.recovered}</h3>
      </div>
    </>
  )
}

export default EachCountry
